.container{
    position: relative;
    top:0;
    left: 0;
    width: 250px;
    height: 350px;
}
.photo{
    height: 100%;
    width: 100%;
    border-radius: var(--border-radius-medium);
    object-fit: cover;
    object-position: center center;
}
.favorite{
    position: absolute;
    top: -15px;
    right: -15px;
    width: 35px;
    height: 35px;
    filter: drop-shadow(0px 0px 2px #000);
    object-fit: cover;
    object-position: center center;
    cursor: pointer;
}