*, *:after, *:before{
  box-sizing: border-box;
}
img{
  display: block;
}
:root {
  --color-white: #fff;
  --color-black: #000;
  --color-dark-grey: #10100e;
  --color-yellow: #ffff00;
  --color-blue: #6a83fb;
  --color-violet: #ae72c2;
  --color-red: #f75d83;

  --width-max: 1200px; 

  --spacing-ultra-small: 5px;
  --spacing-small: 10px;
  --spacing-medium: 20px;
  --spacing-large: 30px;
  
  --transition: 0.4s;
  --font-family-main:sans-serif;
  --font-size-main: 16px;
  --font-size-sub-header: 18px;
  --font-size-header: 26px;
  --font-bold: 600;
  
  --border-radius-small: 3px;
  --border-radius-medium: 5px;

  --margin-list: 7px;

  --text-shadow-yellow: 
    0 0 2px   var(--color-white),
    0 0 40px var(--color-yellow);

  --text-shadow-blue: 
    0 0 2px   var(--color-white),
    0 0 40px var(--color-blue);

  --box-shadow-white:
    0 16px 24px 2px rgba(255, 255, 255, 0.02),
    0 6px 30px 5px rgba(255, 255, 255, 0.04),
    0 8px 10px -5px rgba(255, 255, 255, 0.1);

  --box-shadow-black:
    0 16px 24px 2px rgba(0, 0, 0, 0.5),
    0 6px 30px 5px rgba(0, 0, 0, 0.1),
    0 8px 10px -5px rgba(0, 0, 0, 0.2);

  --theme-light-header: #dfe2df;
  --theme-dark-header: #ca6c6f;
  --theme-neutral-header: #cccc46;

  --theme-default-header: var(--theme-neutral-header);
  

  --theme-light-bgimge: linear-gradient(164deg, rgba(7,31,145,1) 10%, rgba(22,161,214,1) 46%, rgba(183,38,208,0.958420868347339) 100%);
  --theme-dark-bgimge: linear-gradient(to right bottom, #000000, #160b11, #20141e, #281b2e, #2b2440);
  --theme-neutral-bgimge: url(static/bg.jpg) ;
  --theme-default-bgimge: var(--theme-neutral-bgimge);

}

body {
  font-family: var(--font-family-main);
  font-size: var(--font-size-main);
  margin: 0;
  padding: 0;
  background-color: var(--color-dark-grey);
  background-image: var( --theme-default-bgimge);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top left;
  background-attachment: fixed;
}

img{
  display: block;
}

.header__text{
  line-height: 1.7;
  font-size: var(--font-size-header);
  text-shadow: 0 0 2px var(--color-black);
  color: var(--theme-default-header);
  transition: color var(--transition);
}

*{
  scrollbar-color: rgba(37, 56, 97), rgba(0, 0, 0, 0.7);
  scrollbar-width:  thin;
}


::-webkit-scrollbar{
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-thumb{
  background-color: rgba(37, 56, 97);
  border-radius: 2px;
}
::-webkit-scrollbar-track{
  background-color:rgba(0, 0, 0, 0.7);
}
::-webkit-scrollbar-corner{
  background-color:rgba(0, 0, 0, 0.7);
}